<template>
    <v-layout row>
        <v-flex xs12>
            <v-container fluid>
                <v-layout>
                    <v-carousel hide-controls interval="2000" height="100px" hide-delimiters>
                        <v-carousel-item
                                v-for="(item,i) in items"
                                :key="i"
                                :src="item.src"
                        ></v-carousel-item>
                    </v-carousel>
                </v-layout>

                <v-layout row v-for='(musicItem,i) in  musicList' :key="`${i}`" @click="selectList(i)">
                    <v-flex xs12 sm6 offset-sm3>
                        <div to="/music" style="line-height: 56px;padding: 5px 0;border-bottom: 1px saddlebrown solid">
                            <img :src="musicItem.coverImgUrl" height="50px" style="vertical-align: middle">
                            <span v-html="musicItem.title" style='padding:5px'></span>
                        </div>

                    </v-flex>
                </v-layout>

            </v-container>
        </v-flex>

    </v-layout>

</template>

<script>

    export default {
        mounted() {
            var url = this.$store.state.api.music.musicLists.url;
            var data = this.$store.state.api.music.musicLists.data;
            var vm = this;
            this.axios.get(url, {
                params: data
            }).then(data => {
                vm.musicList = data.data.data;
                if (vm.musicList.length > 0) {
                    this.$store.state.api.music.musicList.data.id = vm.musicList[0].id;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        data: () => ({
            sheet: false,
            picker: null,
            musicList: null,
            items: [
                {
                    src: "https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg",
                },
                {
                    src: '//img3.c.yinyuetai.com/others/admin/181109/0/ca4adfc7bac531243a287f4674a4a5b1_0x0.jpg'
                },
                {
                    src: '//img1.c.yinyuetai.com/others/admin/181108/0/0ca6b1c009629a9227406691f43e6a70_0x0.jpg'
                },
                {
                    src: 'https://cdn.vuetifyjs.com/images/carousel/planet.jpg'
                }
            ]
        }),
        methods: {
            selectList(index) {
                var musicId = this.musicList[index].id;
                var vm = this;
                this.$store.state.api.music.musicList.data.id = musicId;
                vm.$router.push({
                    path: '/music/' + musicId
                })
            }
        },
        watch: {
            picker(val) {

            }
        }
    }

</script>
<style>
    .container-content {
        height: 100%;
        width: 100%;
    }
</style>
