<template>
    <v-app fill-height>
        <v-navigation-drawer
                v-model="drawer"
                fixed
                temporary
        >
            <v-list class="pa-1">
                <v-list-tile avatar>
                    <v-list-tile-avatar>
                        <img src="./assets/logo.svg">
                    </v-list-tile-avatar>

                    <v-list-tile-content>
                        <v-list-tile-title>FE—LI</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>

            <v-list class="pt-0" dense>
                <v-divider></v-divider>

                <v-list-tile
                        v-for="item in items"
                        :key="item.title"
                        @click=""
                        :to="item.url"
                >
                    <v-list-tile-action>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-tile-action>

                    <v-list-tile-content>
                        <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>
        </v-navigation-drawer>
        <v-toolbar app
                   :color="$store.state.appTopFooterColor"
                   fixed
        >
            <commonTop @drawerEvent="drawerCtrl"></commonTop>
        </v-toolbar>
        <v-content app style="height: 100%">
            <v-container fluid fill-height style="{'background-color': $store.state.appTopFooterColor}"
                         class="container">
                <vue-scroll :ops="ops"
                            @refresh-start="handleRS"
                            @load-before-deactivate="handleLBD"
                            @load-start="handleLoadStart">
                    <!--未完成-->
                    <router-view></router-view>
                </vue-scroll>
            </v-container>
        </v-content>
        <v-footer app height="56px" fluid>
            <commonFooter></commonFooter>
        </v-footer>
    </v-app>
</template>

<script>
    import commonTop from './components/commonTop'
    import commonFooter from './components/commonFooter'


    export default {
        name: 'App',
        components: {
            commonTop,
            commonFooter
        },
        data() {
            return {
                drawer: null,
                refresh : 0,
                noData : false,
                ops: {
                    vuescroll: {
                        mode: 'slide',
                        pullRefresh: {
                            enable: true
                        },
                        pushLoad: {
                            enable: true,
                            auto: true,
                            autoLoadDistance: 10
                        }
                    }
                },
                items: [
                    {title: '主页', icon: 'dashboard', url: "/"},
                    {title: '播放器', icon: 'question_answer', url: "/music/dawd"}
                ],
            }
        },
        methods: {
            drawerCtrl() {
                console.log(this.drawer)
                this.drawer = !this.drawer;
            },
            handleRS(vsInstance, refreshDom, done) {

                setTimeout(() => {
                    this.refresh++;
                    done();
                }, 1500);
            },
            handleLoadStart(vm, dom, done) {
                setTimeout(() => {
                    const random = Math.floor(Math.random() * 2) + 1;
                    if (random == 1) {
                        this.noData = true;
                    } else {
                        this.noData = false;
                    }
                    done();
                }, 600);
            },
            handleLBD(vm, loadDom, done) {
                setTimeout(() => {
                    if (!this.noData) {
                        this.itemAmount += 2;
                    }
                    done();
                }, 500);
            }
        }
    }
</script>
<style scoped>
    .container {
        padding: 8px 0;
    }
</style>
