import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/music/:id",
      name: "music",
      component: () =>
        import( "./views/Music.vue")
    }
  ]
});
