<template>
        <v-container style="padding: 0;height: 100%">
            <v-layout justify-space-between align-center row fill-height >
                <v-flex xs2>
                    <div
                            slot="activator"
                            @click="clickPernsonal"
                            dark
                    >
                        <v-icon>account_circle</v-icon>
                    </div>
                </v-flex>
                <v-flex xs8 style="text-align: center">
                    <v-combobox
                            v-model="searchVal"
                            chips
                            label="搜索歌曲"
                            style="padding:0 12px"
                            fill-height
                    ></v-combobox>
                    <!--<span>{{$store.getters.appPageName}}</span>-->
                </v-flex>
                <v-flex xs2 style="text-align: right">

                </v-flex>

            </v-layout>

        </v-container>
</template>

<script>
    export default {
        data: () => ({
            sheet: false,
            isEditing : true,
            searchVal : "",
            states : [],
        }),
        methods: {
            clickPernsonal() {
                this.$emit("drawerEvent");
            }
        },
        watch : {
            searchVal (val){
                console.log(val)
                if(!val ){
                    return ;
                }
                var url = this.$store.state.api.music.searchMusicList.url;
                var data = this.$store.state.api.music.musicLists.data;
                data.s = val;
                var vm = this;
                this.axios.get(url, {
                    params: data
                }).then(data => {
                    var musicArr = [];
                    data.data.data.forEach((item,index) => {
                        var musicDetail =  {
                            title: item.name,
                            artist: '推荐',
                            src: item.url,
                            pic: item.pic,
                            lrc: item.lrc
                        };
                        musicArr.push(musicDetail);
                    })
                    vm.$store.state.musicList =  null;
                    vm.$nextTick(function () {
                        vm.$store.state.musicList = musicArr;
                    })

                    // vm.$router.push({
                    //     path: '/music/' + "search"
                    // })
                }).catch(err => {
                    console.log(err);
                })
            }
        }
    }
</script>

<style scoped>
   .v-toolbar__content{
       padding: 0;
   }
</style>
